<template>
  <div class="rater-list">

    <div class="page-wrap">

      <div class="weui-panel weui-panel_access" v-for="item in partiList">
        <div class="weui-panel__bd">
          <div class="weui-panel weui-panel_access">
            <div class="weui-panel__hd">No.{{item.meetno}} {{item.meetingType}}</div>

            <div class="weui-panel__bd">

              <router-link :to="getUrl(item)" style="color:black">
              
              <div class="weui-media-box weui-media-box_text">
                <h4 class="weui-media-box__title">{{item.bu}} - {{item.meetingTitle}}</h4>
                <h2 v-if="item.finalLevel != null" class="weui-media-box__title">等级：{{item.finalLevel}}</h2>
                <p v-if="item.comitTime == null" 
                  class="weui-media-box__desc" style="color:red">剩余{{item.expireDay}}天
                </p>

                <p v-if="item.comitTime != null" 
                    class="weui-media-box__desc" style="color:green">已提交
                </p>

                <ul class="weui-media-box__info">
                  <li class="weui-media-box__info__meta">发布者：</li>
                  <li class="weui-media-box__info__meta">{{item.author}}</li>
                  <li class="weui-media-box__info__meta weui-media-box__info__meta_extra"></li>
                </ul>
              </div>
              </router-link>
            </div>

          </div>
           </div>
      </div>
    </div>

  </div>

</div>
</template>

<script>
import server from '../assets/js/write.js'


export default {
 
  name: 'rater-list',

  data() {
    return {
      selected: '2',
      badge:this.$store.state.user.badge,
      partiList:[],
      value:'',
      search:'false'
    };
  },
  mounted(){
   
       var this2 = this;

       server.messaged(this.badge).then(
        function(response) {
            this2.partiList = response.data;
        });
  },

  methods:{

    getUrl:function(item){

      if(item.comitTime == null) return '/text?id='+item.participantId+'&wordslimit='+item.wordslimit;
      if(item.comitTime != null) return '/checkrate?id='+item.participantId;

      return '#';
    },

  },

  computed: {
    
  }
};
</script>

<style scoped>

.weui-media-box__title{
  white-space: normal;
}

</style>


